@import-normalize;
header.header {
  display: flex;
  justify-content: center; }
  header.header img.header__logo {
    display: block;
    width: 75%;
    min-width: 376px;
    max-width: 676px;
    padding: 1.2rem; }

main.giphy {
  display: flex;
  flex-direction: column !important;
  align-items: center;
  justify-content: center;
  margin: 1.2rem; }

form#search {
  display: flex;
  justify-content: center;
  width: 80%;
  min-width: 376px;
  max-width: 676px; }
  form#search input.search__field {
    width: calc(100% - 100px);
    padding: 10px;
    border: 2px solid #AB41C1;
    border-right-width: 0px;
    background-color: #fefefe;
    font-size: 16px; }
  form#search button.search__button {
    border-width: 0px;
    height: 100%;
    width: 52px;
    color: #fff;
    background-image: url("images/search.gif");
    background-size: cover;
    cursor: pointer; }

.gallery {
  margin-top: 24px;
  max-width: 976px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around; }

.gallery__item {
  display: inline-block;
  height: 180px;
  margin: 4px 0; }
  .gallery__item img {
    display: block;
    height: 100%;
    border-radius: 2px;
    border: 1px solid #eee; }

footer.footer p.footer__copyright {
  text-align: center; }

footer.footer a {
  color: #AB41C1; }
